import React, { useState, useEffect, useCallback } from 'react'
import { useHistory } from 'react-router-dom';
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import Button from '../../components/Button/Button'
import BSC from '../../assets/img/BNB.svg'
import ARBITRUM from '../../assets/img/arbitrum-logo.svg'
import POLYGON from '../../assets/img/polygon-logo.svg'
import ETH from '../../assets/img/ethereum.svg'
import ZKSYNC from '../../assets/img/zkSync-logo.svg'
import stakedLogo from './img/staked.svg'
import errorimg from '../../assets/img/ErrorImg.png'
import './card.less'
import styled, { StyleSheetManager } from 'styled-components';
import useContract from '../../hooks/useContractLp';
import { BigNumber } from 'ethers';
import { bg, getAmountETHAndDeri, getProvider } from '../../utils/utils';
import axios from 'axios';
const DeriAddress = "0x21E60EE73F17AC0A411ae5D690f908c3ED66Fe12"
const ETHAddress = "0x82af49447d8a07e3bd95bd0d56f35241523fbab1"
const PairAddress = "0x96C3966534b4377691e11e163A0409Ad01667343"
export default function Card({ lang, pool, wallet }) {
  const history = useHistory();
  const defaultList = ['311860', '311904']
  const [poolInfo, setPoolinfo] = useState({})
  const [address, setAddress] = useState(pool.address)
  const [isStaked, setIsStaked] = useState(false)
  const [xvsNum, setXvsNum] = useState()
  const [show, setShow] = useState(true)
  const [aTimeRewardInfo, setATimeRewardInfo] = useState([])
  const [bTimeRewardInfo, setBTimeRewardInfo] = useState([])
  const [totalYearDeri, setTotalYearDeri] = useState(0)
  const [accountEthTokenValue, setAccountEthTokenValue] = useState(0)
  const [apy, setApy] = useState()
  const [accountDeriToken, setAccountDeriToken] = useState(0)
  const DeriforPairContract = useContract("ERC20", 42161, DeriAddress)
  const EthforPairContract = useContract("ERC20", 42161, ETHAddress)
  const PoolContract = useContract("arbitrumLp", 42161)
  const BTokenContract = useContract("LpTokenId", 42161)
  const PairContract = useContract("PairAbi", 42161)
  const getPoolInfo = useCallback(async () => {
    setPoolinfo({})
    let res = await pool.getPoolInfo(pool.address, pool.chainId, pool.version)

    setAddress(pool.address)
    setPoolinfo(res)

  }, [pool])
  const getLiquidity = useCallback(async () => {
    setPoolinfo({})
    let DeriNumber = await DeriforPairContract.balanceOf(PairAddress)
    let EthNumber = await EthforPairContract.balanceOf(PairAddress)
    let deriPath = `${process.env.REACT_APP_INFO_HTTP_URL}/stats_for_token`
    let resp = await fetch(deriPath);
    let res = await resp.json();
    let ethPath = "https://sig.oraclum.io/unsigned?symbols=ETHUSD"
    let eresp = await axios.get(ethPath);
    let ethPrice = eresp.data["ETHUSD"]
    let deriPrice = res.data.price;
    let deriAmount = BigNumber.from(DeriNumber._hex).toString() / (10 ** 18);
    let ethAmount = BigNumber.from(EthNumber._hex).toString() / (10 ** 18);
    let deriValue = bg(deriAmount).times(deriPrice).toNumber();
    let ethValue = bg(ethAmount).times(ethPrice).toNumber();
    let liquidity = deriValue + ethValue
    console.log("getLiquidity", liquidity)
    setPoolinfo({
      liquidity: liquidity,
    })
  }, [DeriforPairContract, EthforPairContract])
  const getVenusEarned = async () => {
    if (pool && (pool.version === 'v3' || pool.version === 'v3_lite')) {
      let res = await pool.getVenusEarned(wallet, pool.address)
      setXvsNum(res)
    }
  }
  const getIsStaked = useCallback(async () => {
    let res = await pool.getIsStaked(wallet, pool)
    if (res) {
      setIsStaked(res.isStaked)
      setAddress(res.poolAddress)
    }
  }, [wallet, pool])

  const bTime = useCallback(async (id) => {
    let res;
    await setTimeout(async () => {
      let provider = getProvider(42161);
      let contract = await PoolContract.connect(provider)
      console.log("bTime", contract)
      let data = defaultList.map(async (item) => {
        let obj = {}
        let res = await contract.getSecondsPerLiquidityInsideX128(item)
        if (res) {
          obj["id"] = item
          obj["timestamp"] = BigNumber.from(res.timestamp._hex).toString()
          obj["secondsPerLiquidityInsideX128"] = BigNumber.from(res.secondsPerLiquidityInsideX128._hex).toString()
        }
        return obj
      })
      data = await Promise.all(data)
      setBTimeRewardInfo(data)
    }, 1000);
    return res
  }, [PoolContract])

  const aTime = useCallback(async () => {
    let data = defaultList.map(async (item) => {
      let obj = {}
      let res = await PoolContract.getSecondsPerLiquidityInsideX128(item)
      if (res) {
        obj["id"] = item
        obj["timestamp"] = BigNumber.from(res.timestamp._hex).toString()
        obj["secondsPerLiquidityInsideX128"] = BigNumber.from(res.secondsPerLiquidityInsideX128._hex).toString()
      }
      console.log("aTime res", res)
      return obj
    })
    data = await Promise.all(data)
    bTime()
    setATimeRewardInfo(data)
  }, [PoolContract])

  const getBTokenValue = useCallback(async () => {
    let data = defaultList.map(async (item) => {
      let obj = {}
      let res = await BTokenContract.positions(item)
      obj = {
        tickLower: res.tickLower,
        tickUpper: res.tickUpper,
        liquidity: BigNumber.from(res.liquidity._hex).toString()
      }
      return obj
    })
    data = await Promise.all(data)
    let tickCurrent = await PairContract.slot0()
    tickCurrent = tickCurrent.tick
    let amount = data.map((item) => {
      let [total_amount0, total_amount1] = getAmountETHAndDeri(tickCurrent, item.tickLower, item.tickUpper, item.liquidity)
      return {
        deri: total_amount0 / 10 ** 18,
        eth: total_amount1 / 10 ** 18
      }
    })
    let deriSum = amount.reduce((accumulator, currentValue) => {
      return accumulator + currentValue.deri;
    }, 0)
    let ethSum = amount.reduce((accumulator, currentValue) => {
      return accumulator + currentValue.eth;
    }, 0)
    let ethPath = "https://sig.oraclum.io/unsigned?symbols=ETHUSD"
    let eresp = await axios.get(ethPath);
    console.log("ETHUSD", eresp.data)
    let ethPrice = eresp.data["ETHUSD"]
    let ethValue = bg(ethSum).times(ethPrice).toNumber();
    setAccountEthTokenValue(ethValue)
    setAccountDeriToken(deriSum)
  }, [BTokenContract, defaultList, PairContract])

  const getApy = useCallback(async () => {
    if (totalYearDeri && accountDeriToken && accountEthTokenValue) {
      let deriPath = `${process.env.REACT_APP_INFO_HTTP_URL}/stats_for_token`
      let resp = await fetch(deriPath);
      let res = await resp.json();
      let deriPrice = res.data.price;
      let deriValue = bg(accountDeriToken).times(deriPrice).toNumber();
      let sumValue = deriValue + accountEthTokenValue
      let totalYearDeriValue = bg(totalYearDeri).times(deriPrice).toNumber();
      let apy = (totalYearDeriValue / sumValue) * 100
      setApy(apy)
    }
  }, [totalYearDeri, accountDeriToken, accountEthTokenValue])

  const getTimeRewardInfo = useCallback(async () => {
    console.log("getTimeRewardInfo", aTimeRewardInfo, bTimeRewardInfo)
    let rewards = bTimeRewardInfo.map(async (item) => {
      let aTime = aTimeRewardInfo.find(id => id.id === item.id)
      let secondsPerLiquidityInsideX128_a = aTime.secondsPerLiquidityInsideX128
      let reward = await PoolContract.getRewardInBetween(item.id, item.timestamp, secondsPerLiquidityInsideX128_a, item.secondsPerLiquidityInsideX128)
      if (reward) {
        let obj = {
          reward: BigNumber.from(reward._hex).toString() / 10 ** 18,
          id: item.id,
          timestamp: Math.abs(item.timestamp - aTime.timestamp)
        }
        return obj
      }
    })
    rewards = await Promise.all(rewards)
    console.log("getTimeRewardInfo", rewards)
    if (rewards[0].timestamp !== 0) {
      const totalScore = rewards.reduce((accumulator, currentValue) => {
        return accumulator + currentValue.reward;
      }, 0);
      const oneYear = 1000 * 60 * 60 * 24 * 365;
      const valuePerYear = (totalScore / (rewards[0].timestamp * 1000)) * oneYear;
      setTotalYearDeri(valuePerYear)
    }
  }, [aTimeRewardInfo, bTimeRewardInfo, PoolContract])
  const onImgError = (event) => {
    event.target.src = errorimg
  }
  const closeSymbol = (address) => {
    sessionStorage.setItem(address, false)
    setShow(false)
  }
  useEffect(() => {
    if (pool.version === 'ulp' && DeriforPairContract) {
      getLiquidity()
    } else {
      getPoolInfo()
    }
    if (wallet.isConnected()) {
      getIsStaked()
      getVenusEarned()
    }
  }, [pool, wallet.detail.account, DeriforPairContract, EthforPairContract])
  useEffect(() => {
    let sess = sessionStorage.getItem(pool.address)
    if (sess !== null) {
      if (sess) {
        setShow(false)
      }
    }
  }, [pool])
  const networkLogo = (chainId) => {
    let src;
    if (chainId === "56" || chainId === "97") {
      src = BSC
    } else if (chainId === "137") {
      src = POLYGON
    } else if (chainId === "1") {
      src = ETH
    } else if (chainId === '42161' || chainId === '421611' || chainId === '421613') {
      src = ARBITRUM
    } else if (chainId === '324') {
      src = ZKSYNC
    }
    return src
  }
  const staking = (url) => {
    if (pool.version === 'v2_lite_open') {
      return
    }
    history.push(url)
  }

  useEffect(() => {
    if (pool.version === "ulp" && PoolContract) {
      setATimeRewardInfo([])
      setBTimeRewardInfo([])
      setApy()
      aTime()
    }
  }, [pool.version, PoolContract])
  useEffect(() => {
    if (pool.version === "ulp" && BTokenContract && PairContract) {
      getBTokenValue()
    }
  }, [BTokenContract, pool.version, PairContract])

  useEffect(() => {
    if (totalYearDeri && accountDeriToken && accountEthTokenValue) {
      getApy()
    }
  }, [totalYearDeri, accountDeriToken, accountEthTokenValue])

  useEffect(() => {
    if (aTimeRewardInfo.length && PoolContract && bTimeRewardInfo.length) {
      getTimeRewardInfo()
    }
  }, [aTimeRewardInfo, bTimeRewardInfo, PoolContract])
  const chainMap = {
    "BSC": "BNB Chain",
    "ZKSYNC": "zkSync Era"
  }
  const url = pool.version === 'ulp' ? `/lppool/${pool.chainId}/${pool.address}` : `/pool/${pool.version || 'v1'}/${pool.chainId}/${pool.type}/${pool.address}`
  return (
    <div className={`${pool.address === '0x243681B8Cd79E3823fF574e07B2378B8Ab292c1E' ? 'showGuide' : ''} card-box`}>
      <div className={pool.version === 'v2_lite_open' ? 'card-list open-list' : 'card-list'} onClick={() => { staking(url) }} >
        <div className={`card-header ${pool.retired ? `${pool.version}` : ""} ${pool.new ? "new" : ''}`}>
          <div className='pool-network-type'>
            <div className='network-logo'>
              <img alt='' src={networkLogo(pool.chainId)} />
            </div>
            <div className='pool-type-network-text'>
              <div className='pool-type-text'>
                {pool.address !== '0xD2D950e338478eF7FeB092F840920B3482FcaC40' && <>
                  {lang[pool.type]} {lang[pool.zone]}
                </>}
                {pool.address === "0x1eF92eDA3CFeefb8Dae0DB4507f860d3b73f29BA" && <>
                  DERI-based
                </>}
                {pool.address === '0xD2D950e338478eF7FeB092F840920B3482FcaC40' && <>
                  {lang[pool.zone]}
                </>}
              </div>
              <div className='pool-network-text'>
                {chainMap[pool.chain.toUpperCase()] ? chainMap[pool.chain.toUpperCase()] : pool.chain.toUpperCase()}
              </div>
            </div>
          </div>
          <div className='base-token-logo'>
            {pool.isLp && <>
              <div className='lp-base-token-bg'>
                <div className={`base-token-bg ${pool.bTokens[0].bTokenSymbol}`}></div>
                <span className='lp-con'>+</span>
                <div className={`base-token-bg ${pool.bTokens[1].bTokenSymbol}`}></div>
              </div>
            </>}
            {!pool.isLp && pool.version !== 'v3' && pool.version !== "v3_lite" && <>
              {pool.bTokens.map((item, index) => <div key={index} className='base-token-box-bg no-animation-tokens'>
                <div className={`base-token-bg ${item.bTokenSymbol}`}>
                  {pool.version === 'v2_lite_open' && <>
                    <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-finance/deri-open-zone/main/img/${item.bTokenSymbol}.png`} />
                  </>}
                </div>
              </div>)}
            </>}
            {(pool.version === "v3" || pool.version === "v3_lite") && (!pool.singleBtoken) &&
              <TokenExplode bTokens={pool.bTokens} />
            }
            {
              (pool.version === "v3" || pool.version === "v3_lite") && (pool.singleBtoken) &&
              <div className='base-token-box-bg no-animation-tokens'>
                <div className={`base-token-bg ${pool.bTokens[0].bTokenSymbol}`}>
                </div>
              </div>
            }
          </div>
        </div>
        <div className='card-content'>

          <div className='max-apy'>
            {pool.version !== 'v2_lite_open' && <>
              <div className='max-apy-title'>
                {(pool.version === 'v2' || pool.version === "v3_lite" || pool.version === "v3") ? lang['max-apy'] : lang['apy']}
              </div>
              <div className='max-apy-num'>
                <DeriNumberFormat value={pool.version === "ulp" ? apy : poolInfo.apy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
              </div>
            </>}
          </div>
          <div className='total-pool-liquidity'>
            <div className='total-liquidity-title'>
              {lang['total-pool-liquidity']}
            </div>
            <div className='total-liquidity-num'>
              <DeriNumberFormat value={poolInfo.liquidity} displayType='text' thousandSeparator={true} decimalScale={pool.type === 'lp' ? pool.version === "ulp" ? 0 : 7 : 0} />
            </div>
          </div>
          <div className='your-deri-epoch'>
            {isStaked && address === pool.address && <div className='staked-box'>
              <img src={stakedLogo} alt='' />
              {lang['staked']}
            </div>}
            {(+xvsNum > 0) && (pool.version === 'v3' || pool.version === 'v3_lite') && <span className='xvs-collectable'><div className='staked-box'><DeriNumberFormat width='50px' value={xvsNum} displayType='text' thousandSeparator={true} decimalScale={2} /> &nbsp; XVS  {lang['collectable']}</div></span>}
          </div>
          <div className='stake-button'>
            <Button disabled={pool.version === 'v2_lite_open' ? true : false} className='button' outline label={pool.version === 'v2_lite_open' ? lang['operating'] : lang['stake']} />
          </div>
        </div>
      </div>
      {/* {pool.version === "v3_lite" && show &&
        <div className='new-symbol'>
          <div className='new-symbol-text'>
            New Listing : <a rel="noreferrer" href='https://deri.io/#/trade/futures/FITFIUSDT' target="_blank" >FITFIUSDT</a> <a href='https://deri.io/#/trade/futures/AVAXUSDT' rel="noreferrer" target="_blank">AVAXUSDT</a>
          </div>
          <div className='close-symbol'>
            <img src={close} alt='close' onClick={() => closeSymbol(pool.address)} />
          </div>
        </div>
      } */}
    </div>
  )
}


const TokenWrapper = styled.div`
  @-webkit-keyframes explode {
    0% {
        transform: translateX(0px);
        -moz-transform : translateX(0px);
    }
    100% {
        transform: translateX(100px);
        -moz-transform : translateX(100px);
    }
  }

  @keyframes explode {
    0% {
        transform: translateX(0px);
        -moz-transform : translateX(0px);
    }
    100% {
        transform: translateX(100px) ;
        -moz-transform : translateX(100px) ;
    }
  }
  .base-token{
    margin-left: 0px !important;
    z-index : -2;
    visibility: hidden;
    background-color: #111e34;
    border : 1px solid #fff;
  }
  @media only screen and (max-width: 760px) {
    .base-token{
      display:none;
    }
  }
  .base-token-box-bg.explode:hover .move  {
    -webkit-animation : explode .4s .0s linear;
    -moz-animation :  explode .4s .0s linear;
    animation: explode .4s .0s linear;
    visibility: visible;    
  }
  
  .base-token-box-bg.explode:hover .base-token {
    z-index : 2 !important;
  } 

`
const Token = styled.div`
  position: absolute;
  transform : ${props => `rotate(${-props.rotate}deg)`};
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  .move {
    transform : translateX(100px) ${props => `rotate(${props.rotate}deg)`};
  }
`

const TokenExplode = ({ bTokens = [] }) => {
  const angle = 360 / bTokens.length
  return (
    <TokenWrapper className='v3-tokens'>
      <div className='tokens'>
        {bTokens.filter((t, i) => i < 3).map((token, index) => (
          <div className='base-token-box-bg' key={index}>
            <div className={`base-token-bg ${token.bTokenSymbol}`}>
            </div>
          </div>
        ))}
      </div>
      <div className='base-token-box-bg explode'>
        <div className={'base-token-bg num-token'}>
          {bTokens.length - 3} +
        </div>
        {bTokens.map((token, index) => {
          const curRotate = angle * index
          return (
            <Token className='base-token' rotate={curRotate} width='100' key={index}>
              <div width='100' rotate={curRotate} className={`base-token-bg move ${token.bTokenSymbol}`} >
              </div>
            </Token>
          )
        })}
      </div>
    </TokenWrapper>
  )
}